let state = {
    userInfo:{
        rdID:180001,
        rdName:'',
        rdSex: '',
        rdType: 20,
        rdDept: '',
        rdPhone: '13677277721',
        rdEmail: 'ghoulcn@vip.qq.com',
        rdDateReg: '2018-12-1 9:00:00',
        rdPhoto:'data:image/png;charset=utf-8;base64,',
        rdStatus: '',
        rdBorrowQty: 1,
        rdAdminRoles: 0,
    },
    // imagePlaceholder:',
    userStatusTable:[
        {'statusKey':'','statusValue':1},
        {'statusKey':'','statusValue':2},
        {'statusKey':'','statusValue':0},
    ],
    userTypeTable: [
        {typeKey: '', typeValue: 10},
        {typeKey: '', typeValue: 20},
        {typeKey: '', typeValue: 21},
        {typeKey: '', typeValue: 30},
        {typeKey: '', typeValue: 31},
    ],
    userAdminRoleTable:[
        {'roleKey':'','roleValue':0},
        // {'roleKey':'','roleValue':1},
        {'roleKey':'','roleValue':2},
        {'roleKey':'','roleValue':4},
        {'roleKey':'','roleValue':8},
    ],
    bookStatusTable:[
        {'statusKey':'','statusValue':0},
        {'statusKey':'','statusValue':1},
        {'statusKey':'','statusValue':2},
        {'statusKey':'','statusValue':3},
        {'statusKey':'','statusValue':4}
    ],
    bookLanguageTable:[
        {'languageKey':'','languageValue':0},
        {'languageKey':'','languageValue':1},
        {'languageKey':'','languageValue':2},
        {'languageKey':'','languageValue':3},
        {'languageKey':'','languageValue':4},
        {'languageKey':'','languageValue':5}

    ],
    rules: {
        nameRules: [
            v => !!v || '',
            v => (v && v.length <= 10) || ''
        ],
        emailRules: [
            v => !!v || 'E-mail ',
            v => /.+@.+/.test(v) || ''
        ],
        selectRules: [
            v => (!!v||v===0) || '',
        ],
        deptRules: [
            v => !!v || ' ',
        ],
        phoneRules: [
            v => /^\d+$/.test(v) || '',
            v => (v && v.length === 11) || ''
        ],
        pwdRules: [
            v => /^[a-z0-9A-Z!@#%&_*\-\[\]$(){}=^|;:?]/.test(v) || '6',
            v => (v && v.length >= 6) || '6'
        ],
        bookNameRules: [
            v => !!v || '',
        ],

        bookCoverRules:[
            v => !!v || '',
        ]
    },
    loading:false,
    loginDialog:false,
    alertDialog: false,
    alertDialogContent: {title: '', context: ''},
    confirmDialog:false,
    confirmDialogContent:{title:'',context:'',check:false,deleteCid:''},
    bindDialog: false,
    bindDialogContent: {title: '', rdInfo:{}},
};

export default state


